@charset "UTF-8";

* {
  margin: 0;
  padding: 0;
}

.header__logo {
  width: 100%;
  height: 111px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #ffffff;
}

.header__logo--image {
  width: 201px;
  height: 52px;
}

.viewfinder {
  background: linear-gradient(#bfcdd0, #dde9ed);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 475px;
}

.viewfinder__card {
  position: relative;
  background-color: #ffffff;
  height: 367px;
}

.viewfinder__button {
  position: absolute;
  top: -40px;
  left: 0;
  width: 75px;
  height: 30px;
}

.viewfinder__button-reset {
  background-color: #bfcdd0;
  border-radius: 15px;
  border: 1px solid #ffffff;
  text-transform: uppercase;
  font-family: 'OpenSans',serif;
  font-size: 12px;
  color: #54585a;
  padding: 5px 10px;
}

.viewfinder__button-icono {
  margin-right: 5px;
  width: 11px;
  height: 20px;
}

.viewfinder__card-title {
  display: flex;
}

.viewfinder__card-title-titlebox {
  display: flex;
  flex-direction: column;
}

.viewfinder__card-title-rectangule {
  height: 42px;
  width: 5px;
  background-color: #a2deaf;
  margin: 20px 15px 0 0;
}

.viewfinder__card-title-name {
  padding: 20px 14px 5px 0px;
  font-family: 'ComicSans', sans-serif;
 /*ojo mirar la fuente bien*/
  font-size: 18px;
  line-height: 20px;
  color: #114e4e;
}

.viewfinder__card-title-job {
  padding: 0 14px 5px 0px;
  font-family: 'Comic Sans', sans-serif;
 /*ojo mirar la fuente bien*/
  font-size: 18px;
  line-height: 20px;
  color: #54585a;
 /*a√±adir variable*/;
}/*quitar es para que marque la posicion de la foto sin internet */.viewfinder__photo {
  width: 240px;
  height: 200px;
  background-color: purple;
}/*quitar */.viewfinder__icons {
  display: flex;
  justify-content: flex-start;
  margin: 10px 0 10px 10px;
}

.viewfinder__icons-box {
  margin-left: 10px;
  border-radius: 22px;
  border: 2px solid #a2deaf;
  width: 32px;
  height: 32px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #114e4e;
}

.viewfinder__tags {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin: 5px 20px;
}

.viewfinder__tags-text {
  width: 36px;
  height: 18px;
  font-size: 13px;
  border-radius: 4px;
  background-color: #438792;
  margin-right: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #ffffff;
  text-transform: uppercase;
  padding: 0 5px;
}

.main__header {
  width: 250px;
  margin: 94px auto 0 auto;
}

.main__intro .main__title {
  text-align: center;
  font-size: 19px;
  color: #e17334;
  font-family: 'Merriweather', serif;
  font-weight: 900;
  margin: 35.4px auto 4px auto;
}

.main__intro .main__paragraph {
  font-family: 'OpenSans-Regular',serif;
  font-size: 15px;
  font-weight: 500;
  margin: 0 auto;
  text-align: center;
  color: #114e4e;
  width: 265px;
}

.main__intro .main__list {
  display: flex;
  justify-content: space-around;
  align-items: center;
  border: 1px solid #e17334;
  width: 268px;
  height: 74px;
  margin: 10px auto;
  border-radius: 4px;
}

.main__intro .main__list-item {
  text-decoration: none;
  list-style: none;
  padding: 10px;
  text-align: center;
}

.main__intro .main__list .main__list--text {
  color: #54585a;
  font-size: 16px;
  padding-top: 10px;
}

.main__intro .main__list .icon {
  color: #e17334;
  font-size: 28px;
}

.main__intro .main__button {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 30px 0;
}

.main__intro .main__button--start {
  font-size: 16px;
  font-family: 'OpenSans', serif;
  width: 129px;
  height: 42px;
  background-color: #438792;
  border: none;
  border-radius: 21px;
  color: #ffffff;
  text-transform: uppercase;
}

.footer {
  display: flex;
  flex-direction: column;
  text-align: center;
  height: 110px;
}

.footer__copyright {
  font-family: 'Open Sans', sans-serif;
  font-size: 14px;
  color: #d8d8d8;
  margin-top: 20px;
}

.footer__logo {
  margin-top: 10px;
  margin-bottom: 20px;
}

.footer__logo--image {
  width: 103px;
  height: 40px;
}

.dropdown__desing {
  padding: 20px 30px 10px 31px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.dropdown__desing--angleUp {
  text-align: right;
  padding-right: 20px;
}

.dropdown__desing--name {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.dropdown__desing--name .dropdown__desing--nameText {
  padding-left: 10px;
  color: #54585a;
  font-size: 18px;
  font-family: 'OpenSans-Regular', serif;
  font-weight: 500;
  text-transform: uppercase;
}

.dropdown__desing--name .dropdown__desing--nameIcon {
  padding-right: 10px;
  color: #e17334;
}

@media all and (min-width: 768px) {
  .header__logo {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding-left: 40px;
  }

  .footer {
    flex-direction: row;
    justify-content: space-between;
    height: 60px;
  }

  .footer__copyright {
    margin-left: 40px;
  }

  .footer__logo {
    margin-right: 40px;
  }
}
